import React, { useEffect } from 'react'
import HeaderComponent from '../../components/HeaderComponent/HeaderComponent'
import styleModule from './index.module.scss'
import roomImg from '../../assets/images/003-room.png'
import staffImg from '../../assets/images/001-manager.png'
import chefImg from '../../assets/images/002-chef.png'
import revenue from '../../assets/images/001-revenue.png'
import LineChart from '../../components/LineChartComponent/LineChartComponent'
const Admin = () => {








    return (
        <div>

            <HeaderComponent />

            <div className={styleModule.body}>
                <div className={styleModule.statistical}>
                    <div className={styleModule.chart_container_circle}>
                        <div>
                            <div className={styleModule.pie_chart}></div>
                            <div className={styleModule.legend}>
                                <div className={styleModule.food}><span></span> n (35%)</div>
                                <div className={styleModule.may}><span></span>My (45%)</div>
                                <div className={styleModule.room}><span></span> Phng (20%)</div>
                            </div>
                        </div>
                    </div>
                    <div className={styleModule.parameter} style={{marginTop:30}}>

                        <div className={styleModule.user}><i className="fa-solid fa-user"></i>s ti khon <span>10000</span></div>
                        <div className={styleModule.user}><i className="fa-solid fa-desktop" />s lng my <span>150</span></div>
                        <div className={styleModule.user}><img src={roomImg} alt="" />s lng phng <span>30</span></div>
                        <div className={styleModule.user}><img src={staffImg} alt="" />s nhn vin <span>70</span></div>
                        <div className={styleModule.user}><img src={chefImg} alt="" />s u bp <span>10</span></div>
                    </div>
                </div>
                <div className={styleModule.container}>
                    <div className={styleModule.container_left}>
                        <div className={styleModule.chart_container}>
                            <LineChart />
                        </div>

                    </div>
                    <div className={styleModule.container_right}>

                        <div className={styleModule.action_group}>
                            <img src={revenue} alt="" />
                            <span>Qun l doanh thu</span>
                        </div>
                        <div className={styleModule.action_group}>
                            <img src={staffImg} alt="" />
                            <span>Qun l nhn vin</span>
                        </div>
                        <div className={styleModule.action_group}>
                            <i className="fa-solid fa-burger"></i>
                            <span>Qun k nh bp</span>
                        </div>
                        <div className={styleModule.action_group}>
                            <i className="fa-solid fa-desktop"></i>
                            <span>Qun l my</span>
                        </div>
                        <div className={styleModule.action_group}>
                            <img src={roomImg} alt="" />
                            <span>Qun l phng</span>
                        </div>
                        <div className={styleModule.action_group}>
                            <i className="fa-solid fa-user"></i>
                            <span>Qun l khch hng</span>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default Admin









